import React from 'react';
import { FaTimes } from 'react-icons/fa';

const AddPopup = ({ show, onClose,message }) => {
    React.useEffect(() => {
        if (show) {
            const timer = setTimeout(() => {
                onClose();
            }, 500); // Changed to 3000ms (3 seconds)
            return () => clearTimeout(timer);
        }
    }, [show, onClose]);

    if (!show) return null;

    const animationKeyframes = `
        @keyframes progress-bar {
            0% { width: 0%; }
            100% { width: 100%; }
        }
    `;

    return (
        <div className="fixed inset-0 flex items-center justify-center bg-black bg-opacity-60 z-50">
            <style>{animationKeyframes}</style>
            <div className="bg-white p-6 rounded-lg shadow-xl relative transform transition-transform duration-300 ease-out scale-95">
               {message}
                <button 
                    onClick={onClose} 
                    className="absolute top-2 right-2 text-red-500 hover:text-red-700 transition-colors duration-200 ease-in-out"
                    aria-label="Close"
                >
                    <FaTimes size={18} />
                </button>
            </div>
        </div>
    );
};

export default AddPopup;
