import React, { useState, useEffect, useContext } from "react";
import axios from "axios";
import AddPopup from "../popup";
import DataContext from "../usecontext/DataContext";
 const CategoryManagement = () => {
  const [categories, setCategories] = useState([]);
  const [name, setName] = useState("");
  const [editId, setEditId] = useState(null);
  const [searchTerm, setSearchTerm] = useState(""); // Search term state
  const apiKey = "https://bwithf.onrender.com"
  const [errmassage, seterrmassage] = useState('')
  const { userrole } = useContext(DataContext);

  // Get token from localStorage
  const token = localStorage.getItem('token');

  // Axios configuration with headers
  const axiosConfig = {
    headers: {
      'authtoken': token
    }
  };

  // Fetch categories based on search term
  const fetchCategories = async (searchTerm = "") => {
    try {
      const res = await axios.get(`${apiKey}/api/category/fetchallcategory`, {
        ...axiosConfig,
        params: { name: searchTerm }, // Send search term as query parameter
      });
      setCategories(res.data);
    } catch (error) {
      console.error("Error fetching categories:", error);
    }
  };

  // Add a category
  const addCategory = async () => {
   let  name1 = name.trim()
    if(name1.length < 3 ){
        return
    }
    try {
      const res = await axios.post(`${apiKey}/api/category/addcategory`, { name }, axiosConfig);
      if(res.data.success){
        seterrmassage(
          <>
        <p className="text-green-600 font-semibold text-lg mb-2">{res.data.message}</p>
                <div className="relative w-full h-2 rounded-full overflow-hidden bg-green-200 mt-4">
                    <div className="absolute top-0 left-0 h-full bg-green-500 animate-progress-bar2" style={{ animationDuration: '0.5s' }}></div>
                </div>
          </>
      );
      handleAddItem( );
      setCategories([...categories, res.data.data]);

      setName("");
      }else{
        
         seterrmassage(  <>
          <p className="text-red-600 font-semibold text-lg mb-2">{res.data.message}</p>
                <div className="relative w-full h-2 rounded-full overflow-hidden bg-red-200 mt-4">
                    <div className="absolute top-0 left-0 h-full bg-red-500 animate-progress-bar2" style={{ animationDuration: '0.5s' }}></div>
                </div>
            </>);
       handleAddItem();

       setName("");
    }     
    } catch (error) {
      console.error("Error adding category:", error);
    }
  };

  // Update a category
  const updateCategory = async (id) => {
    try {
      const res = await axios.put(`${apiKey}/api/category/updatecategory/${id}`, { name }, axiosConfig);
      setCategories(
        categories.map((category) =>
          category._id === id ? res.data.category : category
        )
      );
      setEditId(null);
      setName("");
    } catch (error) {
      console.error("Error updating category:", error);
    }
  };

  // Delete a category
  const deleteCategory = async (id) => {
    try {
      await axios.delete(`${apiKey}/api/category/deletecategory/${id}`, axiosConfig);
      setCategories(categories.filter((category) => category._id !== id));
    } catch (error) {
      console.error("Error deleting category:", error);
    }
  };

  // Fetch categories whenever the search term changes
  useEffect(() => {
    fetchCategories(searchTerm);
  }, [searchTerm]); // Dependency on searchTerm

  
  const [showPopup, setShowPopup] = useState(false);

  const handleAddItem = () => {
      // Simulate an add item action
      setShowPopup(true);
  };

  const handleClosePopup = () => {
      setShowPopup(false);
  };


  return (
    <div className="min-h-screen bg-gray-100 p-8">
       <div className="App">
            
             <AddPopup show={showPopup} onClose={handleClosePopup} message={errmassage}  />
        </div>
      <div className="max-w-2xl mx-auto bg-white p-6 rounded-lg shadow-md">
        <h2 className="text-2xl font-bold mb-6">Manage Categories</h2>

        {/* Search Input */}
        <input
          type="text"
          placeholder="Search categories by name..."
          value={searchTerm}
          onChange={(e) => setSearchTerm(e.target.value)} // Update searchTerm state
          className="mb-4 p-2 border border-gray-300 rounded w-full"
        />

        {/* Add or Update Category Form */}
        <form
          onSubmit={(e) => {
            e.preventDefault();
            editId ? updateCategory(editId) : addCategory();
          }}
          className="mb-6"
        >
        { userrole.canAdd && <div className="flex items-center">
            <input
              type="text"
              value={name}
              onChange={(e) => setName(e.target.value)}
              placeholder="Category Name"
              className="p-2 border rounded-md w-full mr-2"
              required
            />
            <button
              type="submit"
              className="bg-blue-500 text-white p-2 rounded-md"
            >
              {editId ? "Update" : "Add"}
            </button>
          </div>}
        </form>

        {/* Categories List */}
        <ul className="space-y-2 h-[400px]    overflow-y-auto">
          {categories.map((category) => (
            <li
              key={category._id}
              className="flex justify-between items-center bg-gray-200 p-2 rounded-md"
            >
              <span>{category.name}</span>
              <div>
                {userrole.canEdit && <button
                  onClick={() => {
                    setEditId(category._id);
                    setName(category.name);
                  }}
                  className="bg-yellow-500 text-white p-1 rounded-md mr-2"
                >
                  Edit
                </button>}
                {userrole.canDelete &&
                <button
                  onClick={() => deleteCategory(category._id)}
                  className="bg-red-500 text-white p-1 rounded-md"
                >
                  Delete
                </button>}
              </div>
            </li>
          ))}
        </ul>
      </div>
    </div>
  );
};

export default CategoryManagement;
